@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:#222}.wrapper{display:flex;justify-content:center;align-items:center;min-height:100vh}.pokedex{background:#fe0065;border:2px #000 solid;padding:1rem;border-radius:1rem;box-shadow:0 0 10px #000}.pokedex-nav{display:flex;justify-content:space-between}.pokedex-nav div:last-child a,.pokedex-nav div:last-child button,.pokedex-nav div:last-child .button{margin-left:.5rem}.pokedex-nav a,.pokedex-nav button,.pokedex-nav .button{font-size:1rem;box-sizing:border-box;padding:0;width:2rem;height:2rem;text-align:center;vertical-align:middle;line-height:2rem;display:inline-block;font-weight:bold;background:#fd0;border-radius:50%;border:2px #000 solid;color:rgba(0,0,0,.47);text-decoration:none;box-shadow:0 0 10px #ffdd59}.pokedex-nav a .svg-inline--fa,.pokedex-nav button .svg-inline--fa,.pokedex-nav .button .svg-inline--fa{vertical-align:0}.pokedex-nav a:hover,.pokedex-nav a.powered-off:hover,.pokedex-nav button:hover,.pokedex-nav button.powered-off:hover,.pokedex-nav .button:hover,.pokedex-nav .button.powered-off:hover{background:#ebcc00;cursor:pointer}.pokedex-nav a:disabled,.pokedex-nav a.powered-off,.pokedex-nav a.disabled,.pokedex-nav button:disabled,.pokedex-nav button.powered-off,.pokedex-nav button.disabled,.pokedex-nav .button:disabled,.pokedex-nav .button.powered-off,.pokedex-nav .button.disabled{background:#9a8500;cursor:not-allowed;box-shadow:none}.pokedex-screen{background-color:#afe5be;border:3px #000 solid;margin-bottom:1rem;height:15rem;width:15rem;overflow-y:scroll;color:#415643;font-family:"Press Start 2P",cursive;font-size:.6rem}.pokedex-screen.powered-off{background-color:#759a7f;overflow-y:auto}.pokemon-list{margin:0;padding:0;list-style:none}.pokemon-list a{text-decoration:none;color:#415643;padding:1rem;display:inline-block;box-sizing:border-box;width:100%}.pokemon-list a:hover{background:#a0d2ae}.pokemon{text-align:center}.pokemon p{margin:.5rem}.pokemon h2{margin-top:0}.pokemon .pokemon-image{mix-blend-mode:luminosity;-ms-interpolation-mode:nearest-neighbor;image-rendering:-moz-crisp-edges;image-rendering:pixelated}
